import Axios, { AxiosResponse } from 'axios';
import { TreeStructureResponse } from '@/interface/treeStructureResponse';
import { NodeStatusDto } from '@/interface/nodeStatusDto';

export async function getTreeStructure(): Promise<AxiosResponse<TreeStructureResponse>> {
    return await Axios.get<TreeStructureResponse>(
        `${process.env.VUE_APP_REQUEST_URL}/FTTX/services/spantree.php`,
    );
}

export async function getNodeStatus(): Promise<AxiosResponse<NodeStatusDto>> {
    return await Axios.get<NodeStatusDto>(
        `${process.env.VUE_APP_REQUEST_URL}/FTTX/services/apitest.json`,
    );
}
