import { StoreOptions } from 'vuex';
import { TreeStructureResponse } from '@/interface/treeStructureResponse';
import { getTreeStructure } from '@/repository/request.repo';

export interface TreeStructureModel {
    tree: TreeStructureResponse | null;
}
const store: StoreOptions<TreeStructureModel> = {
    state: {
        tree: null,
    },
    getters: {
        tree: ({ tree }) => {
            return tree;
        },
    },
    mutations: {
        updateTree: (state, newValue: TreeStructureResponse) => {
            state.tree = newValue;
        },
    },
    actions: {
        getTreeStructure: async ({ commit }) => {
            const { data } = await getTreeStructure();
            commit('updateTree', data);
        },
    },
};

export default store;
