import { StoreOptions } from 'vuex';
import { NodeStatusDto } from '@/interface/nodeStatusDto';
import { getNodeStatus } from '@/repository/request.repo';

export interface NodeStatusModel {
    nodeStatus: NodeStatusDto | null;
    polling: number;
}

const store: StoreOptions<NodeStatusModel> = {
    state: {
        nodeStatus: null,
        polling: -1,
    },
    getters: {
        nodeStatus: ({ nodeStatus }) => {
            return nodeStatus;
        },
    },
    mutations: {
        updateStatus: (state, status: NodeStatusDto) => {
            state.nodeStatus = status;
        },
    },
    actions: {
        startPolling: async ({ commit, state }) => {
            const initial = await getNodeStatus();
            commit('updateStatus', initial.data);
            if (state.polling === -1) {
                state.polling = setInterval(async () => {
                    const { data } = await getNodeStatus();
                    commit('updateStatus', data);
                }, 50000);
            }
        },
        getNodeStatus: async ({commit}) => {
            const {data} = await getNodeStatus();
            commit('updateStatus', data);
        },
        stopPolling: ({ state }) => {
            if (state.polling !== -1) {
                clearInterval(state.polling);
            }
        },
    },
};

export default store;
